@import url('https://cdn.cache.lol/type/fontawesome-free/css/all.css');
@import url('https://cdn.cache.lol/profiles/icons/omg.lol-icons.css?v=2024-01-09d');
@import url('https://fonts.cdnfonts.com/css/satoshi');


@font-face {
    font-family: 'omg.lol';
    src: url('data:font/woff2;charset=utf-8;base64,') format('woff2');
    font-weight: 900;
    font-style: normal;
    font-display: swap;
}

* {
    box-sizing: border-box;
}

body {
    font-size: 1.2em;
    margin: 0;
    padding: 2em;
    font-family: 'Satoshi', sans-serif;
    /*background: linear-gradient(0deg, #3fb6b6 0%, #d56b86 100%);*/
    background: url("https://files.catbox.moe/yafbz3.gif");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    height: 100%;
    overflow-wrap: break-word;
}

main {
    display: block;
    max-width: 30em;
    border-radius: 1em;
    margin: 0 auto;
    text-align: center;
    background: rgba(255, 255, 255, .5);
    padding: 2em;
}

h1 {
    margin: 0;
}

#profile-items {
    /*border-top: 1px solid #666;*/
    padding: .5em 0 0 0;
    margin: 1.5em 0 0 0;
}

#profile-items :is(h1, h2, h3, h4, h5, h6) {
    text-align: left;
    margin: 0;
}

#profile-items h1 { font-size: 1.2em; }
#profile-items h2 { font-size: 1.2em; }
#profile-items h3 { font-size: 1.2em; }
#profile-items h4 { font-size: 1.1em; }
#profile-items h5 { font-size: 1.1em; }
#profile-items h6 { font-size: 1.1em; }

#profile-picture-container {
    margin: auto;
    color: #555;
    margin-bottom: 1em;
}

#profile-picture {
    border-radius: 0.25em;
    height: 8.7em;
}

#pronouns {
    margin: 0 0 1em 0;
    color: #666;
    list-style-type: none;
}
#pronouns a:link,
#pronouns a:visited,
#pronouns a:hover,
#pronouns a:active {
    color: #666;
}

#details {
    margin: 1em 0;
}

#location {
    display: inline-block;
}

#occupation {
    display: inline-block;
}

#location, #occupation {
    margin: .5em;
    padding: 0 .5em;
}

#bio {
    text-align: center;
    font-size: 110%;
    line-height: 140%;
}

#footer {
    margin: 0;
    margin-top: 5em;
    color: #555;
    text-align: center;
}

#footer a:visited,
#footer a:active {
    color: #555;
    text-decoration: none;
    border: 0;
}

#prami {
    width: 2em;
}

ul {
    text-align: left;
}

li {
    line-height: 170%;
}

a:link,
a:visited {
    color: #000;
    text-decoration-color: #555;
    text-decoration: none;
    border-bottom: 1px dotted #000;
}
a:hover,
a:active {
    color: #000;
    text-decoration-color: #000;
    text-decoration: none;
    border-bottom: 2px solid #000;
}

.logotype {
    color: #f783ac;
    font-family: 'omg.lol';
}

#verification {
    font-size: 80%;
    color: #0089cf;
}

#now {
    margin-top: 2em;
    text-align: left !important;
}

hr {
    border: 0;
    height: 1px;
    background: #333;
    margin: 1.5em 0;
}

@media (max-width: 500px) {
    body {
        font-size: 1em;
        padding: 1em;
    }
    main {
        padding: 1.5em;
    }
}
